<template>
  <div class="nueva_palabra">
   <center><div id="contenedor_form" class="container">
  <center><form @submit.prevent="validar" enctype="multipart/form-data">
          <div class="row clearfix">
          <div class="col-xs-12 col-sm-8">
            <div class="form-group">
              <label class="control-label">Palabra tecnica</label>
              <input type="text" v-model="modelo.palabra_tecnica" class="form-control"  v-validate="'required|alpha_spaces'" placeholder="Base de datos" name="palabra_tecnica" data-vv-as="Palabra Tecnica">
              <i v-show="errors.has('palabra_tecnica')" class="fa fa-warning"></i>
              <span style="color:red;" v-show="errors.has('palabra_tecnica')" class="help is-danger">{{ errors.first('palabra_tecnica') }}</span>
            </div>
          </div>

            <div class="col-xs-12 col-sm-8">
              <div class="form-group">
                <label class="control-label">Unidad Curricular</label>
                <div>
                  <select class="form-control" v-model="modelo.unidad_curricular" name="unidad_curricular" data-vv-as="'unidad_curricular'" >
                    <option disabled value="Seleccione..."> Seleccione... </option>
                    <option>programacion</option>
                  </select>
                  
                </div>
              </div>
            </div>

          <div class="col-xs-12 col-sm-8">
            <div class="form-group">
              <label class="control-label">Archivo de Video</label>
              <input type="file" class="form-control" v-validate="'required'" name="archivo_video"  data-vv-as="Archivo de video" @change="processFile($event)">
              <i v-show="errors.has('archivo_video')" class="fa fa-warning"></i>
              <span style="color:red;" v-show="errors.has('archivo_video')" class="help is-danger">{{ errors.first('archivo_video') }}</span>
            </div>
          </div> 

          <div class="col-xs-12 col-sm-8">
            <div class="form-group">
              <label class="control-label">Extencion</label>
              <input type="text" class="form-control" v-model="modelo.extencion" v-validate="'required|alpha_spaces'" placeholder=".mp4,avi,mpg,3gp" name="extencion" data-vv-as="Extencion">
              <i v-show="errors.has('extencion')" class="fa fa-warning"></i>
              <span style="color:red;" v-show="errors.has('extencion')" class="help is-danger">{{ errors.first('extencion') }}</span>
            </div>
          </div>   

          </div>
          <div class="form-group">
             <div class="row clearfix">
              <div class="col-xs-12">
                <button type="submit" id="bt" class="btn btn-success">Guardar</button>
                <button type="button" id="bt" class="btn btn-warning">Cancelar</button>
              </div>
            </div>
          </div>                          
  </form></center>
  </div></center>
</div>
</template>

<script>
import servicio from '../../servicios/servicios_index'

export default {
  name: 'nueva_palabra',
  components: {
  },
  data () {
    return {
      modelo: {},
      video: ""
    }
  },
  mounted () {
  },
  methods: {
    processFile(event) {
      this.modelo.video = event.target.files[0]
      // this.modelo.name_video = this.video.video.name
    },
    close () {
      this.clearError()
    },
    clearError () {
      this.modelo = {}
      this.$nextTick(() => { this.errors.clear() })
      if (this.esEditar) {
        this.$router.push({
          name: 'base'
        })
      }
    },
    validar () {
      this.$validator.validateAll().then(result => {
        if (result) {
          return this.guardar()
        } 
      })
    },
    guardar(){
      console.log(this.modelo)
      servicio.guardar(this.modelo).then((response) => {
        console.log(response)
      }).catch(() => {
        console.error("PROBLEMAS AL CONECTAR CON EL SERVIDOR")
      })
    }
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<!-- <link rel="stylesheet" type="text/css" href="../style.css"> -->
<style type="text/javascript" scope>
#contenedor_form{
  position: relative;
  left: 100px;
  top: 60px;
  width: 600px;
  height: 400px;
}
#bt{
  position: relative;
  left: 200px;
}
</style>



