<template>
  <div class="hello">
  <br>
  <a href="http://localhost:8080/#/">
<icon name="reply" scale="6" id="arrow_regresar"></icon>
</a>
<center><div id="area_encuesta_documentos" class="container">


    <section class="col-md-auto container">
        <a target="_blank" href="./static/documentos/horario_t3.pdf">
          <div id="documentos" class="container" @change="pdf">
