import http from '../cliente.js'

export default {
  guardar (params) {
    return http.post(`http://127.0.0.1/AIDA/control/controlador.php?option=save`, params)
    .then(response => response.data)
  },

  consultar () {
    return http.get(`http://127.0.0.1/AIDA/control/controlador.php?option=query`)
    .then(response => response.data)
  },

  eliminar (id) {
    return http.get(`http://127.0.0.1/AIDA/control/controlador.php?option==delete&pk=${id}`)
    .then(response => response.data)
  },

  buscar (id) {
    return http.get(`http://127.0.0.1/AIDA/control/controlador.php?option==search&pk=${id}`)
    .then(response => response.data)
  },

  editar (params) {
    return http.post(`http://127.0.0.1/AIDA/control/controlador.php?option=update`, params)
    .then(response => response.data)
  },  

  login (params) {
    return http.get(`http://127.0.0.1/AIDA/control/login.php?option=login`, params)
    .then(response => response.data)
  }
}


