          <div class="col-xs-12 col-sm-6">
            <div class="form-group">
              <label class="control-label">Usuario</label>
              <input type="text" v-model="modelo.usuario" class="form-control"  v-validate="'required'" placeholder="jose2130" name="usuario">
              <i v-show="errors.has('usuario')" class="fa fa-warning"></i>
              <span v-show="errors.has('usuario')" class="help is-danger" style="color:red;">Requerido</span>
            </div>
          </div>
          
          <div class="col-xs-12 col-sm-6">
            <div class="form-group">
              <label class="control-label">Contraseña</label>
              <input type="password" v-model="modelo.clave" class="form-control"  v-validate="'required'" placeholder="*************" name="clave">
              <i v-show="errors.has('clave')" class="fa fa-warning"></i>
              <span v-show="errors.has('clave')" class="help is-danger" style="color:red;">Requerido</span>
            </div>
          </div>          

            <div id="error_campos_vacios" v-if="modal3">
              <label></label>
              <center>
              <img height="60px" width="60px" :src="img_static + x.png"><br>
              <b style="color:red;">Por favor llene todos los campos</b><br><br>
              <b></b></center>
            </div>


        </div>
           <div class="row clearfix" id="btn_login">
