<script>
import servicio from '../servicios/servicios_index'

export default {
  name: 'base',
  components: {
  },
  data () {
    return {
      showModal_intro: false,
      img_static: './static/img/',
      name_img: 'desarrollo.png',
      modal1: false,
      modal2: false,
      modal3: false,
      modelo: {}
    }
  },
  mounted () {
    // setTimeout(() => { this.close() }, 5000)
  },
  methods: {
    panel_login () {
      this.showModal_intro = true
      this.modal1 = false
      this.modal2 = true
    },
    intro () {
      this.showModal_intro = true
      this.modal2 = false
      this.modal1 = true
      this.clearError()
    },
    close () {
      this.showModal_intro = false
      this.modal2 = false
      this.modal1 = false
      this.clearError()
    },
    clearError () {
      this.modal2 = false
      this.modelo = {}
      this.$nextTick(() => { this.errors.clear() })
      if (this.esEditar) {
        this.$router.push({
          name: 'base'
        })
      }
    },
    validar () {
      this.$validator.validateAll().then(result => {
        if (result) {
            return this.guardarRegistro()
        } else {
          this.modals.modal5 = true
          this.showModal = true
          this.limpiar()
        }
      })
    },
    guardarRegistro () {
      servicio.login(this.modelo).then((resp) => {
        console.log(resp.data)
      }).catch(() => {
        console.error("PROBLEMAS AL CONECTAR CON EL SERVIDOR")
      })
    }
  }
}
</script>

