      <div id="opciones_li" @click="nueva_palabra_f"><icon name="pencil-square-o" scale="4" id="icon_nueva_palabra" color="#2E9AFE"></icon><br>Nueva Palabra</div>
      <div id="opciones_li" @click="nuevo_documento_f"><icon name="file-o" scale="4" id="icon_nuevo_documento" color="#2E9AFE"></icon><br>Nuevo Documento</div>
    </ul>
  </div>

