<script>
import NuevaPalabra from './nueva_palabra.vue'
import NuevoDocumento from './nuevo_documento.vue'

export default {
  name: 'dashboard',
  components: {
    NuevaPalabra,
    NuevoDocumento
  },
  data () {
    return {
      nueva_palabra: false,
      nuevo_documento: false
    }
  },
  mounted () {
  },
  methods: {
    nueva_palabra_f () {
      this.nueva_palabra = true
      this.nuevo_documento = false
    },
    nuevo_documento_f () {
      this.nuevo_documento = true
      this.nueva_palabra = false
    }
    
  }
}
</script>

